
.products{
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  justify-items: center;
  margin: 20px;
  z-index: -1;
  justify-content: space-around;  
}
.category{
  position: sticky;
  top: 0;
}
  .home_imge{
    width: 100%; 
    z-index: -1;
    margin-bottom: -10px;
    mask-image: linear-gradient(to bottom, rgba(0,0,0,1), rgba(0,0,0,0));
  }
  
  .row_category{
    font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    font-size: large;
    display: flex;
    justify-content: space-around;
    }
  @media(max-width: 576px){
    .row_category{
    display: flex;
    justify-content: space-around;
      font-size: small
    }
  }

/*----------Product Item ---------*/

@import url("../utils/productItem/productItem.css");  

.delete-all{
  text-align: right;
  margin: 20px;
}
.delete-all input{
  height: 25px;
  width: 25px;
  transform: translateY(5px);
  margin: 0 15px;
}
.delete-all span{
  text-transform: uppercase;
  color: rgb(3, 165, 206);
  letter-spacing: 1.3px;
}
.delete-all button{
  border: 1px solid crimson;
  padding: 10px 25px;
  color: crimson;
  text-transform: uppercase;
}

/*-----------filters Menu----------*/
@media(max-width: 576px){
  .category{
    font-size:small;
  }

  .filter_menu input{
    font-size: small;
    background: #f5f5f5;
  }
}

.filter_menu{
  width: 100%;
  min-height: 40px;
  overflow: hidden;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  margin: 15px 0px ;
}
.filter_menu select, input{
  border: 1px solid #ccc;
  outline: none;
  height: 40px;
  margin: 0 25px;
  border-radius: 8px;
  background: #f5f5f5;
}
.filter_menu input{
  flex: 1;
  margin: 0 5px;
  border-radius: 10px;
  background: #f5f5f5;
}
.filter_menu select {
  opacity: 0.7;
}

/*----------Load More----------*/
.load_more{
  text-align: center;
}
.load_more button{
  padding: 10px 25px;
  margin-bottom: 20px;
  border: 1px solid #555;
  text-transform: capitalize;
}
