.card-container{
    justify-content: space-around;
    margin-bottom: 50px;
}
.card-container .img{
    height: 400px;
    display: block;
    width: 100%;
    object-fit: cover;
}
 .card:hover{
    z-index: 1;
    background: rgb(243, 234, 234);
    box-shadow: 0 8px 50px rgba(0,0,0,0.2);
    transform: scale(1.05);
}


@media(max-width:576px){
    .card-container{
        display: block;
        justify-content: space-around;      
    }
    .card-container .img{
        height: 100px;
        display: block;
        width: 100%;
        object-fit: cover;
    }
    .card-container .title{
        font-size: small;
    }
}
